import React, { useRef } from "react";
import { Link,useHistory,useLocation} from "react-router-dom";
import "../Login.css";
import useAuth from "../../hooks/useAuth";
import axios from "axios";

function Register() {
  const emailRef = useRef();
  const passRef = useRef();
  const nameRef = useRef();
  const history=useHistory();
  const location=useLocation();
  const {createUser} = useAuth();
  const handleLogin = (e) => {
    const name = nameRef.current.value;
    nameRef.current.value = "";
    const email = emailRef.current.value;
    emailRef.current.value = "";
    const password = passRef.current.value;
    passRef.current.value = "";
    const loginData = {
      name,
      email,
      password,
    };
   createUser(loginData.email, loginData.password, history,location,name)
   axios.post(`https://enigmatic-fjord-26508.herokuapp.com/users`,loginData)
   .then(result=>console.log(result))
    // alert("register button clicked");
    e.preventDefault();
  };
  return (
    <div className="container form-design">
      <h1>Create Account</h1>
      <form action="" className="form-child" onSubmit={handleLogin}>
        <div class="form-floating mb-3">
          <input
            type="text"
            class="form-control"
            id="floatingInput"
            placeholder="name@example.com"
            ref={nameRef}
            required
          />
          <label for="floatingInput">Name</label>
        </div>
        <div class="form-floating mb-3">
          <input
            type="email"
            class="form-control"
            id="floatingInput"
            placeholder="name@example.com"
            ref={emailRef}
            required
          />
          <label for="floatingInput" className="fs-4">
            Email address
          </label>
        </div>
        <div class="form-floating">
          <input
            type="password"
            class="form-control"
            id="floatingPassword"
            placeholder="Password"
            ref={passRef}
            required
          />
          <label for="floatingPassword">Password</label>
        </div>
        <div>
          <button type="submit" className="btn btn-primary w-100">
            Submit
          </button>

          <p>
            Already Regitered ?<Link to="/login">Login</Link>
          </p>
        </div>
      </form>
    </div>
  );
}

export default Register;
