import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";

function AllProducts() {
  const [allProducts, setallProducts] = useState([]);
  const [isload, setIsload] = useState(true);
  useEffect(() => {
    axios.get("https://enigmatic-fjord-26508.herokuapp.com/products").then((result) => {
      setallProducts(result.data);
      setIsload(false);
    });
  }, []);

  if (isload) {
    return (
      <div className="d-flex align-items-center justify-content-center h-100 position-absolute w-100">
        <div
          class="spinner-border"
          style={{ width: "4rem ", height: "4rem" }}
          role="status"
        ></div>
      </div>
    );
  }

  return (
    <div>
      <div className="container mt-5">
        <h1 className="my-5">THE WORLDâ€™S BEST LUXURY WATCH BRANDS</h1>
        <div class="row row-cols-1 row-cols-md-3 g-4">
          {allProducts.map((product) => (
            <div class="col">
              <div class="card h-100 border-0">
                <img src={product.img} class="card-img-top" alt="..." />
                <div class="card-body">
                  <h5 class="card-title">{product.name}</h5>
                  <p class="card-text">{product.desc}</p>
                  <h5>Price: {product.price}$</h5>
                  <Link to={`/placeorder/${product._id}`}>
                    {" "}
                    <button className=" product-about w-75 fw-bold">
                      <span>
                        <i class="fas fa-cart-plus"></i>
                      </span>{" "}
                      SHOP NOW
                    </button>
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default AllProducts;
